<template>
  <div class="loginView">
    <form class="bd">
      <ul>
        <li>
          <span>账号:</span><input type="text" v-model="userID" ref="login" />
        </li>
        <li>
          <span>密码:</span
          ><input
            type="password"
            v-model="userPassword"
            @keydown.enter="login"
          />
        </li>
        <li>{{ tips }}</li>
        <li><div class="send" @click="login">确认登录</div></li>
        <li class="zc">
          <router-link to="/register">还没有账号前去注册</router-link>
        </li>
      </ul>
    </form>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      userID: "",
      userPassword: "",
      tips: " ",
    };
  },
  mounted() {
    this.$refs.login.focus();
  },
  methods: {
    login() {
      if (this.userID.trim() != "" && this.userPassword.trim() != "") {
        this.tips = " ";
        this.$store.dispatch("getlogin");
        let loginData = {
          user_name: this.userID,
          user_psw: this.userPassword,
        };
        this.$bus.$on("loginState", (date) => {
          if (date >= 0) {
            this.$router.push({
              name: "home",
            });
          }
        });
        this.$store.commit("VERIFI", [this, loginData]);
      } else {
        this.tips = "账号和密码不能为空";
      }
    },
  },
};
</script>

<style scoped>
.loginView {
  width: 100%;
  height: 100vh;
  background-color: #eea2a4;
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 345px;
}
.bd {
  box-sizing: border-box;
  padding: 50px;
  border: 2px solid white;
  border-radius: 13px;
}
.bd li {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  height: 65px;
}
.bd li span {
  display: block;
  font: 700 1rem "";
}
.bd input {
  outline: none;
  border: 0;
  height: 40px;
  margin-left: 10px;
  border-radius: 20px;
  box-sizing: border-box;
  padding: 10px;
}
.bd .send {
  box-sizing: border-box;
  padding: 10px;
  border: 1px solid white;
  border-radius: 12px;
  cursor: pointer;
  user-select: none;
}
.zc > * {
  font: 1.1rem "";
  color: white;
  text-decoration: none;
}
</style>
